import React, {Component} from 'react';
import $ from "jquery";

export default class Header_menu extends Component {
    componentDidMount(){
    $(function() {
        $(".toggle").on("click", function() {
            if ($(".item").hasClass("active")) {
                $(".item").removeClass("active");
                $(this).find("a").html("<i className='fas fa-bars'></i>");
            } else {
                $(".item").addClass("active");
                $(this).find("a").html("<i className='fas fa-times'></i>");
            }
        });
    });
}
    render() {
        return (
            <div className="header_menu">
                <nav>
                    <ul className="menu">
                        <li className="logo"><a href="#">Facebook App</a></li>
                        <li className="item"><a href="#"><i className="fas fa-home"/>Home</a></li>
                        <li className="item"><a href="#"><i className="fas fa-address-card"/>About</a></li>
                        <li className="item"><a href="#"><i className="fab fa-usps"/>Services</a></li>
                        <li className="item"><a href="#"><i className="fas fa-wrench"/>Features</a></li>
                        <li className="item"><a href="#"><i className="fas fa-blog"/>Blog</a></li>
                        <li className="item"><a href="#"><i className="fas fa-wifi"/>Contact</a></li>
                        <li className="item"><i className="fas fa-sign-out-alt"/></li>
                        <li className="item"><i className="fas fa-users-cog"></i></li>
                        <li className="toggle"><a href="#"><i className="fas fa-bars"></i></a></li>
                    </ul>
                </nav> 
            </div>     
        )
      }
    }